import Footer from "../components/UI/Footer/Footer";
import Navbar from "../components/UI/Navbar/Navbar";
import styles from './layout.module.css';

export default function RootLayout({ children }) {
    return (
            <html>
                <body>
                    <div id="root">
                        <Navbar />
                        <div className={styles.content}>
                            {/* <AnimatedOutlet/> */}
                            {children}
                        </div>
                        <Footer />
                    </div>
                </body>
            </html>
    );
}